"use client";
import { useSongFind } from "@/services/song/useSongFind";
import PageContainer from '@/app/(DashboardLayout)/components/container/PageContainer';
import { useState, useMemo, useEffect } from "react";
import {
  CircularProgress,
  Alert,
  Stack,
  Typography,
  TextField,
  Button,
  Grid,
  Card,
  CardContent,
  CardMedia,
  CardActionArea,
} from "@mui/material";
import Select from "@mui/material/Select";
import MenuItem from "@mui/material/MenuItem";
import FormControl from "@mui/material/FormControl";
import InputLabel from "@mui/material/InputLabel";
import Link from "next/link";
import { useRouter, usePathname, useSearchParams } from 'next/navigation';
import { Box } from "@mui/material";
import { useTranslation } from "react-i18next";
import Pagination from "@mui/material/Pagination";
import { Song } from "@/types/song";
// Hook ƒë·ªÉ debounce gi√° tr·ªã, tr√°nh g·ªçi API/URL update li√™n t·ª•c
const useDebounce = (value: string, delay: number) => {
  const [debouncedValue, setDebouncedValue] = useState(value);
  useEffect(() => {
    const handler = setTimeout(() => {
      setDebouncedValue(value);
    }, delay);
    return () => {
      clearTimeout(handler);
    };
  }, [value, delay]);
  return debouncedValue;
};

export default function SongsPage() {
  const router = useRouter();
  const pathname = usePathname();
  const searchParams = useSearchParams();
  const { t } = useTranslation();

  // ƒê·ªçc state t·ª´ URL ho·∫∑c d√πng gi√° tr·ªã m·∫∑c ƒë·ªãnh
  const [page, setPage] = useState(Number(searchParams.get('page')) || 1);
  const [pageSize, setPageSize] = useState(Number(searchParams.get('limit')) || 10);
  const [sort, setSort] = useState(searchParams.get('sort') || "createdAt DESC");
  const [searchTerm, setSearchTerm] = useState("");
  const debouncedSearchTerm = useDebounce(searchTerm, 500);

  // X√¢y d·ª±ng object `where` t·ª´ state
  const whereObject = useMemo(() => {
    const where: { [key: string]: any } = {};
    const initialWhere = JSON.parse(searchParams.get('where') || '{"name": ""}');
    const filter = debouncedSearchTerm || initialWhere.name?.contains || '';
    if (filter) {
      where.name = { contains: filter };
    }
    return where;
  }, [debouncedSearchTerm, searchParams]);

  // ƒê·ªìng b·ªô state v√†o URL
  useEffect(() => {
    const params = new URLSearchParams();
    params.set('page', String(page));
    params.set('limit', String(pageSize));
    params.set('sort', sort);
    if (Object.keys(whereObject).length > 0) {
      params.set('where', JSON.stringify(whereObject));
    }
    // D√πng replace ƒë·ªÉ kh√¥ng t·∫°o th√™m entry trong history c·ªßa tr√¨nh duy·ªát
    router.replace(`${pathname}?${params.toString()}`);
  }, [page, pageSize, sort, whereObject, pathname, router]);

  // G·ªçi hook v·ªõi c√°c tham s·ªë ri√™ng bi·ªát theo chu·∫©n Sails.js
  const { songs, pagination, isLoading: loading, isError: error } = useSongFind(page, pageSize, whereObject, sort);

  if (loading) return <div>Loading songs...</div>;
  if (error) return <Alert severity="error">ƒê√£ x·∫£y ra l·ªói khi t·∫£i d·ªØ li·ªáu.</Alert>;

  return (
    <PageContainer seo={{
      title: t('SONGSPAGE_TITLE') + " - " + t('PAGE') + " " + page,
      description: t('SONGSPAGE_DESCRIPTION') + " - " + t('PAGE') + " " + page,
      keywords: t('SONGSPAGE_KEYWORDS'),
      thumb: ""
    }}>
      <Box sx={{ p: 3 }}>
        <Typography variant="h4" fontWeight="bold" mb={4}>{t('SONGSPAGE_TITLE')}</Typography>

        <Grid container spacing={2} mb={4} alignItems="center">
          <Grid item xs={12} md>
            <Stack direction="row" spacing={1}>
              <TextField
                label="Search by title..."
                value={searchTerm}
                onChange={(e) => { setSearchTerm(e.target.value); setPage(1); }}
                variant="outlined"
                size="small"
                fullWidth
              />
            </Stack>
          </Grid>
          <Grid item xs={6} md="auto">
            <FormControl variant="outlined" size="small" fullWidth sx={{ minWidth: 180 }}>
              <InputLabel id="sort-select-label">Sort By</InputLabel>
              <Select
                labelId="sort-select-label"
                value={sort}
                onChange={(e) => setSort(e.target.value as string)}
                label="Sort By"
              >
                <MenuItem value={"createdAt DESC"}>Newest</MenuItem>
                <MenuItem value={"title ASC"}>Title (A-Z)</MenuItem>
                <MenuItem value={"title DESC"}>Title (Z-A)</MenuItem>
              </Select>
            </FormControl>
          </Grid>
          <Grid item xs={6} md="auto">
            <FormControl variant="outlined" size="small" fullWidth sx={{ minWidth: 120 }}>
              <InputLabel id="page-size-select-label">Show</InputLabel>
              <Select
                labelId="page-size-select-label"
                value={pageSize}
                onChange={(e) => setPageSize(e.target.value as number)}
                label="Show"
              >
                <MenuItem value={5}>5</MenuItem>
                <MenuItem value={10}>10</MenuItem>
                <MenuItem value={20}>20</MenuItem>
              </Select>
            </FormControl>
          </Grid>
        </Grid>

        <Grid container spacing={3}>
          {songs && songs.map((song: Song) => (
            <Grid item xs={12} sm={6} md={4} lg={3} key={song.id}>
              <Card sx={{ height: '100%', display: 'flex', flexDirection: 'column' }}>
                <CardActionArea component={Link} href={`/song?id=${song.id}`}>
                  <CardMedia
                    component="img"
                    sx={{ height: 160 }}
                    image={song.thumb || "/default-cover.png"}
                    alt={song.title}
                  />
                  <CardContent>
                    <Typography gutterBottom variant="h6" component="div" noWrap>
                      {song.title}
                    </Typography>
                    <Typography variant="body2" color="text.secondary">
                      {song.artist?.name || 'Unknown Artist'}
                    </Typography>
                  </CardContent>
                </CardActionArea>
              </Card>
            </Grid>
          ))}
        </Grid>

        {/* === Khu v·ª±c Ph√¢n trang === */}
        {pagination && pagination.totalPage > 1 && (
          <Stack spacing={2} mt={4} alignItems="center">
            <Pagination
              count={pagination.totalPage}
              page={page}
              onChange={(event, value) => setPage(value)}
              color="primary"
            />
          </Stack>
        )}
        {songs && songs.length === 0 && (
          <Typography variant="h6" color="text.secondary" sx={{ textAlign: 'center', mt: 4 }}>
            {t('SONGSPAGE_NO_SONG_FOUND')}
          </Typography>
        )}
      </Box>
    </PageContainer>
  );
}
